
  
@mixin desktop{
    @media (max-width:1320px) {
        @content
    }
}

@mixin desktop-below{
    @media (max-width:992px) {
        @content
    }
}

@mixin pad{
    @media (max-width:768px) {
        @content
    }
}

@mixin pad-below{
    @media (max-width:648px) {
        @content
    }
}
.container{
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 12px;

    @include desktop-below(){
        max-width: 768px;
        padding: 0 36px;
    }
    @include pad(){
      max-width: 666px;
      padding: 0 24px;
    }
    @include pad-below(){
      max-width:375px;
      padding: 0 13px;

    }
  }