@import "./grid";

*,*::before,*::after{
    box-sizing: border-box;
    /* outline: 1px solid black; */
  }
  body{
    font-family: 'Roboto', 'Noto Sans TC', sans-serif;
    line-height: 1.5;
  }
  a{
   text-decoration: none;
   display: block;
  }
  a:hover{
    opacity: 0.6;
  }
  
  img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
  }
  header{
    background-color: $lightred;
}
h2{
  font-size: 36px;
  @include pad-below(){
    font-size: 16px;
  }
}
h3{
  font-size: 48px;
  color:$darkred;
  text-align: center;
  font-weight: bold;
  margin-bottom: 32px;
  @include pad(){
    font-size: 36px;
  }
  @include pad-below(){
    font-size: 24px;
    margin-bottom: 24px;
  }
}
h4{
  font-size: 48px;
  font-style: italic;
  color:$lightred;
  font-weight: bold;
  text-align: center;
}
h5{
  margin-bottom: 72px;
}
h6{
  font-size: 48px;
  @include pad-below(){
    font-size: 16px;
  }
}
